import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Task } from '../task';
import { DataStorageService } from '../data-storage.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ParetoComponent } from './pareto/pareto.component';
import { AbcComponent } from './abc/abc.component';
import { EizenhauerComponent } from './eizenhauer/eizenhauer.component';

@Component({
	selector: 'app-main',
	templateUrl: './main.component.html',
	styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
	newTaskName;
	message: string;
	testDataKey: string = 'SMAdata';
	taskList: Task[] = [];
	constructor(private dataStorage: DataStorageService, public dialog: MatDialog) {}

	ngOnInit() {
		if (this.dataStorage.getData(this.testDataKey)) {
			this.taskList = this.dataStorage.getData(this.testDataKey);
			console.log(this.sortTasks(this.taskList));
		} else {
			this.taskList = this.storeTestData();
			console.log(this.sortTasks(this.taskList));
		}
	}
	ngOnDestroy() {
		this.dataStorage.storeData(this.testDataKey, this.taskList);
	}
	public edit(task): void {
		task.editorMode = true;
	}
	public save(task): void {
		task.editorMode = false;
	}
	public delete(task): void {
		this.taskList.splice(this.taskList.indexOf(task), 1);
	}
	public addNew(name): void {
		this.taskList.push({ name: name, priority: 0, isDone: false, editorMode: false, usedMethod: 'none' });
		this.newTaskName = '';
	}
	public handlePriorityChange(taskList: Task[]): void {
		this.sortTasks(taskList);
	}
	public enterAddTask(event, name) {
		if (event.keyCode == 13) {
			this.addNew(name);
		}
	}
	public usePareto(taskList: Task[]) {
		const allTasks = taskList.length;
		const mainTasks = Math.round(allTasks * 0.2);
		const otherTasks = Math.round(allTasks * 0.8);
		const taskWord = mainTasks > 1 ? 'задачах' : 'задачі';
		this.message = `Згідно з принципом парето Вам слід приділити більше уваги і сконцентруватися на ${mainTasks} ${taskWord}, інші ${otherTasks} слід передивитися за пріорітетами`;
		let dialogRef = this.dialog.open(ParetoComponent, {
			width: '320px',
			data: { title: 'Принцип Парето', message: this.message }
		});
	}
	public useAbc(taskList: Task[]) {
		const allTasks = taskList.length;
		const aTasks = Math.round(allTasks * 0.15);
		const bTasks = Math.round(allTasks * 0.2);
		const cTasks = Math.round(allTasks * 0.65);
		let dialogRef = this.dialog.open(AbcComponent, {
			data: {
				title: 'Аналіз АБВ',
				taskList: taskList,
				aTasks: `Потрібна кількість задач для блоку : ${aTasks}`,
				bTasks: `Потрібна кількість задач для блоку : ${bTasks}`,
				cTasks: `Потрібна кількість задач для блоку : ${cTasks}`
			}
		});
	}
	public useEizenhauer(taskList: Task[]) {
		let dialogRef = this.dialog.open(EizenhauerComponent, {
			data: {
				title: 'Метод Ейзенхауера',
				taskList: taskList
			}
		});
	}
	private storeTestData(): Task[] {
		const testData: Task[] = [
			{
				name: 'Задача 1',
				priority: 1,
				isDone: true,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 2',
				priority: 2,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 3',
				priority: 3,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 4',
				priority: 4,
				isDone: true,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 5',
				priority: 5,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 6',
				priority: 6,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 7',
				priority: 7,
				isDone: true,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 8',
				priority: 8,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			},
			{
				name: 'Задача 9',
				priority: 9,
				isDone: false,
				editorMode: false,
				usedMethod: 'none'
			}
		];
		return testData;
	}
	private sortTasks(taskList: Task[]): Task[] {
		taskList.sort((a, b) => {
			return a.priority - b.priority;
		});
		return taskList;
	}
}
